.title-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  padding: 0 16px;
}

.editable-title {
  flex: 1;
}

.editable-title input {
  width: 100%;
  box-sizing: border-box;
  border: 2px solid #1890ff;
  border-radius: 8px;
  padding: 4px 12px;
  font-size: 18px;
}

.editable-title input:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
}