/* 配套CSS */
.title-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  padding: 0 16px;
}

.editable-title {
  flex: 1;
}

.editable-title input {
  width: 100%;
  box-sizing: border-box;
}

.title-icon {
  width: 24px;
  height: 24px;
}