.file-uploader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px dashed #ccc;
  border-radius: 8px;
  padding: 2rem;
  background-color: white;
  min-height: 400px;
  transition: all 0.3s ease;
}

.file-uploader.dragging {
  border-color: #4a90e2;
  background-color: rgba(74, 144, 226, 0.05);
}

.upload-content {
  text-align: center;
  max-width: 500px;
}

.upload-icon {
  font-size: 3rem;
  color: #4a90e2;
  margin-bottom: 1rem;
}

.file-types {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.select-files-btn {
  background-color: #4a90e2;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.select-files-btn:hover {
  background-color: #3a80d2;
}