.subtitle-container {
  display: flex;
  align-items: center;
  margin: 10px 0;
  padding: 5px;
}

.subtitle-icon-circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #e6f3ff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  overflow: hidden;
}

.subtitle-icon {
  width: 24px;
  height: 24px;
  fill: #4a90e2;
}

.subtitle-text-container {
  flex: 1;
}

.subtitle-text-wrapper {
  position: relative;
  display: inline-block;
}

.subtitle-text {
  font-size: 18px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  position: relative;
  display: inline-block;
}

.subtitle-text:hover {
  color: #4a90e2;
}

.subtitle-underline {
  height: 4px;
  background-color: #389fff;
  border-radius: 2px;
  margin-top: 2px;
  transition: width 0.3s ease;
}

.subtitle-text-input {
  font-size: 18px;
  font-weight: bold;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 4px 8px;
  width: 100%;
  outline: none;
}

.subtitle-text-input:focus {
  border-color: #4a90e2;
  box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}